<template>
    <div class="callback">

    </div>
</template>

<script>
export default {
  name: 'callback',
  props: ['query'],
  created() {
    /* eslint-disable no-console */
    this.redirectCallback();
  },
  methods: {
    redirectCallback() {
      const { provider } = this.$route.params;
      const baseUrl = this.query;
      const params = `code=${baseUrl.code}&scope=${
        baseUrl.scope}&authuser=${baseUrl.authuser}&session_state=${
        baseUrl.session_state}&prompt=${baseUrl.prompt}`;

      /* eslint-disable no-console */
      console.log(typeof params);
      this.axios({
        method: 'get',
        baseURL: 'http://localhost:8000',
        url: `/api/login/${provider}/callback?${params}`,
      }).then((response) => {
        /* eslint-disable no-console */
        console.log(response.data);
      }).catch((error) => {
        /* eslint-disable no-console */
        console.log(error.response.data);
      });
    },
  },
};
</script>
